<script>
import axios from 'axios'

export default {
  created(){
    if(this.$session.get('jwt')!=null){
          const jwt = this.$session.get('jwt');
          console.dir(axios.defaults)
          axios.defaults.headers.common['Authorization'] = jwt;
          // console.dir(axios.defaults.headers)
          // jwt   userId, no, type vuex   
          axios.get('/user/reget').then((Response)=>{
            this.$store.dispatch('LOGIN',Response.data); 
          })
      // axios.defaults.withCredentials = true;//CORS    
    }
  },
  computed : {
    userInfo() {
      return this.$store.getters.userInfo
    }
  }
}
