<script>
export default {
	methods: {
		gologin() {
			this.$router.push('/')
		},
		handleWheel() {
			window.addEventListener('wheel', function(event) {
				if (event.deltaY > 0) { //DOWN
					if(window.scrollY<768){
						window.scrollTo({top: 768, left:0, behavior:'smooth'})
					}else if(window.scrollY<1536){
						window.scrollTo({top: 1536, left:0, behavior:'smooth'})
					}else if(window.scrollY<2304){
						window.scrollTo({top: 2304, left:0, behavior:'smooth'})
					}
				} else if (event.deltaY < 0) { //UP
					if(window.scrollY<748){
						window.scrollTo({top: 0, left:0, behavior:'smooth'})
					}else if(window.scrollY<1536){
						window.scrollTo({top: 768, left:0, behavior:'smooth'})
					}else if(window.scrollY<2304){
						window.scrollTo({top: 1536, left:0, behavior:'smooth'})
					}else{
						window.scrollTo({top: 2304, left:0, behavior:'smooth'})
					}
				}
			})
		}
	}
}
</script>

