<template>
  <div class="contentsmain">
    <v-container>
        <v-row>
          <v-col class="left">
            <router-link to="/#"><logo class="logo" style="margin:5% auto"></logo></router-link>
          </v-col>
          <v-col class="right" v-if="userInfo">
            <ul style="margin:5%">
              <template v-if="userInfo.userType==='teacher'">
                <v-btn rounded color="success" @click="classhometeacher" width="7vw"> </v-btn>
              </template>
              <template v-if="userInfo.userType==='student'">
                <v-btn rounded color="success" @click="classhomestudent" width="7vw"> </v-btn>
              </template>
              <v-btn rounded color="primary" @click="logout" width="7vw"></v-btn><br>
              <template v-if="userInfo.userType==='teacher'">
                <v-btn rounded color="error" @click="childrendetail" width="7vw"></v-btn>
              </template>
              <template v-if="userInfo.userType==='student'">
                <v-btn rounded color="error" @click="mystudy" width="7vw"></v-btn>
              </template>
                <v-btn rounded color="warning" @click="draw" width="7vw"></v-btn>
            </ul>
          </v-col>
        </v-row>
    </v-container>
      <br><contents/>
  </div>
</template>

<script>
import Contents from '@/components/Contents.vue'
import Logo from '@/components/Logo.vue'

export default {
  name: 'contentsmain',
  components: {
    Contents,
    Logo
  },
  methods : {
    mystudy(){
      this.$router.push('/childrendetail')
    },
    logout(){
        this.$session.destroy();
        this.$store.dispatch('LOGOUT');
        this.$router.push('/');
    },
    classhometeacher() {
      this.$router.push('/teachermain')
    },
    classhomestudent() {
      this.$router.push('/studentmain')
    },
    childrendetail() {
      this.$router.push('/mychildren')
    },
    draw() {
      this.$router.push('/draw')
    },
  },
  computed : {
    userInfo(){
      return this.$store.getters.userInfo
    }
  }
}
</script>

<style scoped>
div.container {
  padding: 0;
}
.v-img {
  width: 100%;
  height: 100%;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

ul {
  /* display: inline-block; */
  margin-left: 1%;
  padding: 1%;
}
.logo{
  height: 100% !important;
  width: 100% !important;
}
</style>