<template>
  <div>
    <b-row style="margin-left:10px">
        <b-col cols=8>
          
            <p style="margin-top:10px"> : {{classInfo.startDate}} ~ {{classInfo.endDate}}</p>
            <p> : {{classInfo.studentCnt}} /{{classInfo.userLimit}}</p>
            <p> : {{classInfo.classDesc}}</p>
            <b-row style="height: 200px;border:1px solid gray;overflow: auto;"> 
              
                <template v-for="(session,index) in sessionInfo">
                  <b-col cols="12" :key="index">
                     {{session.sessionName}} - {{session.sessionDesc}}
                    <v-icon @click="delSession(session.sessionNo)" v-if="$store.getters.userInfo && $store.getters.userInfo.userType=='teacher' ">
                      mdi-delete
                    </v-icon>
                </b-col>
                </template>

               <b-col>
              </b-col>
              <b-col>
                    <template>
                        <v-row justify="center">
                          <v-dialog
                            v-model="dialog"
                            persistent
                            max-width="600px"
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                color="primary"
                                dark
                                v-bind="attrs"
                                v-on="on"
                              v-if="$store.getters.userInfo && $store.getters.userInfo.userType=='teacher'">
                                
                              </v-btn>
                            </template>
                            <v-card>
                              <v-card-title>
                                <span class="headline">User Profile</span>
                              </v-card-title>
                              <v-card-text>
                                <v-container>
                                  <v-row>
                                    <v-col
                                      cols="12"
                                      sm="6"
                                      md="4"
                                    >
                                      <v-text-field
                                        label=""
                                        required
                                        v-model="sessionName"
                                      ></v-text-field>
                                    </v-col>
                                    <v-col
                                      cols="12"
                                      sm="6"
                                      md="4"
                                    >
                                      <v-text-field
                                        label=""
                                        v-model="sessionDesc"
                                      ></v-text-field>
                                    </v-col>
                                  </v-row>
                                </v-container>
                              </v-card-text>
                              <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                  color="blue darken-1"
                                  text
                                  @click="dialog = false"
                                >
                                  
                                </v-btn>
                                <v-btn
                                  color="blue darken-1"
                                  text
                                  @click="addSession"
                                >
                                  
                                </v-btn>
                              </v-card-actions>
                            </v-card>
                          </v-dialog>
                        </v-row>
                  </template>
              </b-col>
              <b-col>
              </b-col>
             
            </b-row>
        </b-col>
        <b-col cols=4 >
					<v-img :src="$store.getters.BACKEND_URL+classInfo.classImg" aspect-ratio="1" style="margin-right:10px" ></v-img>

        </b-col>

    </b-row>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return {
      classInfo : [],
      sessionInfo : [],
      dialog: false,
    }
  },
  created() {
    this.get_classInfo();
    this.get_classsession();
  },
  methods : {
    get_classInfo(){
      axios.get(`class/oneclass/`+this.$route.params.classno)
        .then((Response)=>{
            this.classInfo = Response.data
            console.dir(this.classInfo);
        })
        .catch((error)=>{
            error
        })
    },
    get_classsession(){
      axios.get(`classsession/selectclasssession/`+this.$route.params.classno)
      .then((Response)=>{
        this.sessionInfo = Response.data
      })
      .catch((error)=>{
        error
      })
    },
    addSession(){
      this.dialog = false
      let data = {
        sessionName : this.sessionName,
        sessionDesc : this.sessionDesc,
        classNo : this.$route.params.classno
      }
      axios.post('classsession/classsession',data)
      .then((Response)=>{
        Response
        alert('  ')
        this.$router.go()
      })
      .catch((error)=>{
        error
      })
    },
    delSession(no){
      axios.delete('classsession/oneclasssession/'+no)
      .then((Response)=>{
        Response
        alert('  ')
        this.$router.go()
      })
      .catch((error)=>{
        error
      })
    }
  }
}
</script>

<style>

</style>