<template>
  <div>
    <template>
    <v-row justify="center">
        <v-dialog
        v-model="dialog"
        
        max-width="600px"
        >
        <template v-slot:activator="{ on, attrs }">
            <v-btn
            color="primary"
            dark
            v-bind="attrs"
            v-on="on"
            >
            
            </v-btn>
        </template>
        <v-card>
            <v-card-title>
            <span class="headline"></span>
            </v-card-title>
            <v-card-text>
            <v-container>
                <v-row>
                <v-textarea
                        background-color="light-blue"
                        color="black"
                        label=" "
                        v-model="name"
                ></v-textarea>
                <v-col cols="12" v-for="(problem,i) of problems" :key="i">
                     <v-textarea
                        background-color="light-blue"
                        color="black"
                        :label="''+ (i + 1) +''"
                        v-model="problem.dictationProblem"
                    ></v-textarea>
                    <voice-recorder :idx='i' @audio-change="audioChange" class="recorder"></voice-recorder>
                </v-col>
                <v-btn @click="add"></v-btn>
                <v-btn @click="sub"></v-btn>
                </v-row>
            </v-container>
            </v-card-text>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                color="blue darken-1"
                text
                @click="dialog = false"
            >
                
            </v-btn>
            <v-btn
                color="blue darken-1"
                text
                @click="addDictionSet"
            >
                
            </v-btn>
            </v-card-actions>
        </v-card>
        </v-dialog>
    </v-row>
    </template>
  </div>
</template>

<script>
import axios from 'axios'
import VoiceRecorder from '@/components/VoiceRecord.vue'

export default {
    components:{VoiceRecorder: VoiceRecorder},
    data(){
        return{
            dialog : false,
            loading: false,
            search: null,
            problems: [{dictationProblem:'', audioData: null}],
            name: "",
            count : 1
          
        }
    },
    watch: {
      search (val) {
        val && val !== this.problems && this.querySelections(val)
      },
    },
    methods: {
      audioChange(i,base64Data){
        this.problems[i].audioData = base64Data
      },
      querySelections (v) {
        this.loading = true
        // Simulated ajax query
        setTimeout(() => {
          this.items = this.states.filter(e => {
            return (e || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
          })
          this.loading = false
        }, 500)
      },
      addDictionSet(){
        // console.log(this.count)
        if(this.problems.length!=this.count){
            alert(' 1')
            return;
        }
        for(let i=0;i<this.problems.length;i++){
            if(this.problems[i]==null){
                 alert(' 2')
                 return;
            }
        }
        this.dialog = false
        let data = {
          dictationsetName : this.name,
          dictationSetContentList : this.problems
        }
        
        axios.post('dictation/createproblem',data)
        .then((Response)=>{
            Response
            alert(' ')
            this.$router.go();
        })
      },
      add(){
        this.count = this.count+1;
        this.problems.push({dictationProblem:'', audioData: null})
      },
      sub(){
        if(this.count==1){
          return;
        }
        this.problems.pop()
        this.count = this.count-1;
      }
    },
}
</script>

