<template>
  <v-app>
		<nav-bar></nav-bar>
			<v-container>
				<v-card style="height:150vh; background-color:white;" elevation="0">
					<v-card class="card-title">
						<v-row>
							<v-col cols="10">
								<h1>{{userInfo.userName}} </h1>
							</v-col>
							<v-col  v-if="userInfo.userType==='student'" cols="2">
							
								<v-dialog v-model="dialog" persistent	max-width="600px">
									<!--  -->
									<!--      -->
									<template  v-slot:activator="{ on, attrs }">
										<v-btn  color="primary" dark v-bind="attrs" v-on="on" style=""> </v-btn>
									</template>
									<!--  -->
									<v-card>
										<v-card-title>
											<span class="headline"> </span>
										</v-card-title>
										<v-card-text>
											<v-container>
												<v-row>
												<v-col cols="12" sm="6" md="4">
													<v-text-field	label="" v-model="parentId" required></v-text-field>
												</v-col>
												</v-row>
											</v-container>
										</v-card-text>

										<v-card-actions>
										<v-spacer></v-spacer>
										<v-btn color="blue darken-1" text @click="dialog = false"></v-btn>
										<v-btn color="blue darken-1" text @click="addParent"></v-btn>
										</v-card-actions>
									</v-card>
								</v-dialog>
							</v-col>
						</v-row>

						<!-- <make-parent></make-parent> -->
					</v-card>
					<v-cards>
						<v-row>
							<v-col>
								<v-card style="height:5vh">
									
								</v-card>
								<v-row>
									<v-col cols="4">
										<div class="pa-7 yellow rounded-circle d-inline-block" style="color:black">{{userStatus.korean}}%</div>	
										<p></p>
									</v-col>
									<v-col cols="4">
										<div class="pa-7 red rounded-circle d-inline-block">{{userStatus.wordtest}}%</div>	
										<p></p>
									</v-col>
									<v-col cols="4">
										<div class="pa-7 green rounded-circle d-inline-block">{{userStatus.dictation}}%</div>	
										<p></p>
									</v-col>
								</v-row>
							
							</v-col>
								
							<v-col cols="7">
								<v-card style="height:180px">
									<p  style="margin-top:10px">?</p>
									<!-- %,    -->
									<p>{{getKoreanStatus(userStatus.korean)}}</p>
									<p>{{getWordStatus(userStatus.wordtest)}}</p>
									<p>{{getDictationStatus(userStatus.dictation)}}</p>
								</v-card>
								<!-- <v-card style="height:5vh"> </v-card>
								<v-row justify="center">
									<v-expansion-panels popout>
										<v-expansion-panel >
											<v-expansion-panel-header>-</v-expansion-panel-header>
											<template v-for="(contentsdetail, index) in contentsdetails" >
												
												<v-expansion-panel-content  :key="index">{{ contentsdetail.name }}{{ contentsdetail.result}}</v-expansion-panel-content>
											</template>
										</v-expansion-panel>
										<v-expansion-panel >
											<v-expansion-panel-header></v-expansion-panel-header>
											<template v-for="(contentsdetail, index) in userStatus.wordtestCate" >
									
												<v-expansion-panel-content  :key="index">{{ contentsdetail.categoryName }}:{{ calcul(contentsdetail.categoryCount,4)}}%</v-expansion-panel-content>
											</template>
										</v-expansion-panel>
										<v-expansion-panel >
											<v-expansion-panel-header></v-expansion-panel-header>
											<template v-for="(contentsdetail, index) in userStatus.dictationLevel" >
												
												<v-expansion-panel-content  :key="index">{{ contentsdetail.dictationLevel }}:{{calcul(contentsdetail.dictationCount,10)}}%</v-expansion-panel-content>
											</template>
										</v-expansion-panel>
									</v-expansion-panels>
								</v-row> -->
							</v-col> 
						</v-row>
						<v-row>
							<v-col>
								<v-card style="height:5vh;">
									  
								</v-card>
								<v-row style="height:25vh;">
									<v-expansion-panels popout>
										<v-expansion-panel v-for="(dictation, i) in dictation_results" :key="i">
											<v-expansion-panel-header>
												<v-row>
													<v-col cols="11">{{dictation.dictationNo}} </v-col>
												</v-row>
											</v-expansion-panel-header>
											<v-expansion-panel-content>
												<div style="text-align:left;">
													<p>:{{isCorrect(dictation.correct)}}</p>
													<img style="width:900px"  class="ocrimgs" :src="$store.getters.BACKEND_URL+dictation.submitImg"/>
												</div>
											</v-expansion-panel-content>
										</v-expansion-panel>
									</v-expansion-panels>
								</v-row>
									
							</v-col>
						</v-row>
					</v-cards>
				</v-card>
			</v-container>
		<foot-bar></foot-bar>
  </v-app>
</template>

<script>
import NavBar from '@/components/NavBar.vue'
import FootBar from '@/components/FootBar.vue'
import axios from 'axios'
import Axios from 'axios'
// import func from '../../vue-temp/vue-editor-bridge'
// import MakeParent from '@/components/MakeParent.vue'
export default {
	name: 'childrendetail',
	components: {
		NavBar,
		FootBar,
		// MakeParent
	},
	data(){
		return {
			dialog: false,
			parentId : "",
			contentsnames: ['-','',''],
			score:["0%","0%","0%"],
			contentsdetails: [
				{ id:'1', name: '', result: '%'},
				{ id:'1', name: '', result: '%' },
				{ id:'1', name: '', result: '%' },
				{ id:'1', name: '', result: '%'},
				{ id:'1', name: '', result: '%' },
			
			],
			dictation_results:[
				
			],
			userStatus:[]


		}
	},created(){
		// alert("1");
		
      if(this.$route.params.userno){
		// console.log("")
		this.getScoreByNo();
		this.getStatusByNo();
		this.getDictationByNo();
		
      }else{
		this.getScore();
		this.getStatus();
		this.getDictation();
      }

		// console.log("11");
    },
	methods : {
		addParent() {
			this.dialog = false
			axios.get(`user/info/${this.parentId}`)
			.then((Response)=>{
				axios.get('user/makeparent/'+Response.data.userNo)
				.then((response)=>{
					response
					alert("   .")
				})
				.catch((error)=>{
					error
					alert("  .")
				})
			})
		},
		getScore(){
			Axios.get("/koreandetail/score").then((response)=>{
						// console.log("");
						// this.contentsdetails[0].result.$set("1%")
						this.contentsdetails[0].result=Math.round((response.data[1]/14)*100)+"%";
						this.contentsdetails[1].result=Math.round((response.data[2]/10)*100)+"%";
						this.contentsdetails[2].result=Math.round((response.data[3]/5)*100)+"%";
						this.contentsdetails[3].result=Math.round((response.data[4]/11)*100)+"%";
						this.contentsdetails[4].result=Math.round((response.data[5]/14)*100)+"%";
						var sum=response.data[1]+response.data[2]+response.data[3]+response.data[4]+response.data[5];
						this.score[0]=Math.round((sum/54)*100)+"%";
						// console.log("1123");
						// console.dir(this.contentsdetails);
                    }).catch((err)=>{
                        err
                    })
		},
		getStatus(){
			Axios.get("/user/userstatus").then((response)=>{
						this.userStatus=response.data;
						// console.log(this.userStatus);
						this.userStatus.dictation=Math.round((this.userStatus.dictation/400)*100);
						this.userStatus.korean=Math.round((this.userStatus.korean/54)*100);
						this.userStatus.wordtest=Math.round((this.userStatus.wordtest/16)*100);
                    }).catch((err)=>{
                        err
                    })
		
		},getDictation(){
			Axios.get("/dictationsubmit/dictationsubmituserno").then((response)=>{
						this.dictation_results=response.data;
			
                    }).catch((err)=>{
                        err
                    })
		
		},getScoreByNo(){
			Axios.get("/koreandetail/scoreuserno/"+this.$route.params.userno).then((response)=>{
						// console.log("");
						// this.contentsdetails[0].result.$set("1%")
						this.contentsdetails[0].result=Math.round((response.data[1]/14)*100)+"%";
						this.contentsdetails[1].result=Math.round((response.data[2]/10)*100)+"%";
						this.contentsdetails[2].result=Math.round((response.data[3]/5)*100)+"%";
						this.contentsdetails[3].result=Math.round((response.data[4]/11)*100)+"%";
						this.contentsdetails[4].result=Math.round((response.data[5]/14)*100)+"%";
						var sum=response.data[1]+response.data[2]+response.data[3]+response.data[4]+response.data[5];
						this.score[0]=Math.round((sum/54)*100)+"%";
						// console.log("1123");
						// console.dir(this.contentsdetails);
                    }).catch((err)=>{
                        err
                    })
		},
		getStatusByNo(){
			Axios.get("/user/userstatusbyuserno/"+this.$route.params.userno).then((response)=>{
						this.userStatus=response.data;
						// console.log(this.userStatus);
						this.userStatus.dictation=Math.round((this.userStatus.dictation/400)*100);
						this.userStatus.korean=Math.round((this.userStatus.korean/54)*100);
						this.userStatus.wordtest=Math.round((this.userStatus.wordtest/16)*100);
                    }).catch((err)=>{
                        err
                    })
		
		},getDictationByNo(){
			Axios.get("/dictationsubmit/dictationsubmitusernobydirect/"+this.$route.params.userno).then((response)=>{
						this.dictation_results=response.data;
			
                    }).catch((err)=>{
                        err
                    })
		
		},
		getKoreanStatus(score){
			
			if(score<=10){
				return "   ,  !";
			}else if(score<=30){
				return "  ";
			}else if(score<=60){
				return "  !"
			}else{
				return "  !"
			}

			
		},getWordStatus(score){
			if(score<=10){
				return "   ,  !";
			}else if(score<=30){
				return "  ";
			}else if(score<=60){
				return "  !"
			}else{
				return "  !"
			}
		},getDictationStatus(score){
			if(score<=10){
				return "   ,  !";
			}else if(score<=30){
				return "  ";
			}else if(score<=60){
				return "  !"
			}else{
				return "  !"
			}
		},calcul(score,index){
			console.dir(Math.round((score/index)*100));
			return Math.round((score/index)*100);
		},isCorrect(bool){
			if(bool){
				return "";
			}else{
				return "";
			}
		}
	},computed:{
		userInfo:function(){
			return this.$store.getters.userInfo;
		}
	}
}
</script>

<style scoped>
.v-card {
	background-color: teal;
	border-radius: 5px;
	padding: 1%;
}
.card-title {
	height: 10%;
	text-align: left;
}

</style>