<template>
  <div>
    <div>
      <router-link to="/#"><logo class="logo"></logo></router-link>
    </div>
    <div class="contact-us">
      <div style="float:right">
        <p style="margin:0"> ? <a href="/dodam"></a></p>
      </div>
        <input placeholder="" required="" type="text" v-model="userId" @keyup.enter="login"/><input name="password" placeholder="" type="password" v-model="userPw" @keyup.enter="login"/>
        <button type="password" @click="login"></button><br>
        <div>
          <p style="margin:0">  ?</p>
          <button @click="join"></button>
        </div>
    </div>
  </div>
</template>

<script>
import Logo from '@/components/Logo.vue'
import axios from 'axios'

export default {
  name: "main",
  components: {
    Logo,
  },
	data: () => ({
		includeFiles: true,
    enabled: false,
    userId : "",
    userPw : "",
  }),
  methods:{
    logout() {
      this.$session.destroy();
      this.$store.dispatch('LOGOUT');
      this.$router.go();
    },
    login() {
      let User = {
        'userId':this.userId,
        'userPw':this.userPw
			}

			var config={
        header:{
          'Content-Type' : 'application/json',
        }
			}
			axios.post('/user/login',User,config)
			.then((response)=>{
				if (response.status === 200 && response.headers.authorization) {

                  this.$session.start();
                  this.$session.set('jwt', response.headers.authorization);
                  axios.defaults.headers.common['Authorization'] = response.headers.authorization;
                  this.$store.dispatch('LOGIN',response.data);
                  if(this.$store.getters.userInfo.userType=='teacher'){
                    this.$router.push('/teachermain');
                  }else{
                    this.$router.push('/contentsmain');
                  }
        }else{
                alert('  ');
        }
			}).catch((err)=>{
				err
				alert('  System Error');
			})
    },
    check_login(){
      if(this.$session.get('jwt')!=null){
        axios.get('user/info')
        .then((Response)=>{
          if(Response.data.userType=='teacher'){
            this.$router.push('/teachermain');
          }
          else{
            this.$router.push('/contentsmain');
          }
        })
      }
    },
    join(){
      this.$router.push('/join')
    }
  },
  computed : {
    userInfo(){
      return this.$store.getters.userInfo
    },
    userType(){
      return this.$store.getters.userInfo.userType
    }
  },
  created() {
    this.check_login()
  }
}
</script>

<style scoped>
.logo{
  width: 100% !important;
  height: 10%;
}
.contact-us {
  font-family: 'Jua', sans-serif;
	width: 40%;
	margin: 10% auto;
  background: #f8f4e5;
  padding: 50px 100px;
  border: 2px solid black;
  box-shadow: 15px 15px 1px #ffa580, 15px 15px 1px 2px black;
}

input {
  display: block;
  width: 100%;
  font-size: 14pt;
  line-height: 28pt;
  font-family: 'Jua', sans-serif;
  margin-bottom: 28pt;
  border: none;
  border-bottom: 5px solid black;
  background: #f8f4e5;
  min-width: 250px;
  padding-left: 5px;
  outline: none;
  color: black;
}

input:focus {
  border-bottom: 5px solid #ffa580;
}

button {
  display: block;
  margin: 0 auto 1%;
  line-height: 28pt;
  padding: 0 20px;
  background: #ffa580;
  letter-spacing: 2px;
  transition: 0.2s all ease-in-out;
  outline: none;
  border: 1px solid black;
  box-shadow: 3px 3px 1px #95a4ff, 3px 3px 1px 1px black;
}
button:hover {
  background: black;
  color: white;
  border: 1px solid black;
}

::selection {
  background: #ffc8ff;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  border-bottom: 5px solid #95a4ff;
  -webkit-text-fill-color: #2A293E;
  -webkit-box-shadow: 0 0 0px 1000px #f8f4e5 inset;
  transition: background-color 5000s ease-in-out 0s;
}
</style>