<template>
  <div>
    <template>
    <v-row justify="center">
        <v-dialog
        v-model="dialog"
        persistent
        max-width="600px"
        >
        <template v-slot:activator="{ on, attrs }">
            <v-btn
            color="primary"
            dark
            v-bind="attrs"
            v-on="on"
            >
            
            </v-btn>
        </template>
        <v-card>
            <v-card-title>
            <span class="headline"></span>
            </v-card-title>
            <v-card-text>
            <v-container>
                <v-row>
                    <v-textarea
                        background-color="light-blue"
                        color="black"
                        label="  "
                        v-model="name"
                    ></v-textarea>
                    <v-col
                        cols="12"
                        v-for="i in count"
                        :key="i"
                    >
                    <div class="noimg">
                        <label for="photo" id="file-drag-not_img" ref="dropbox">
                        <h4>  </h4>
                        <input type="file" id="photo" accept="image/*" @change="onFileChange" @click="index=i"/>
                        </label>
                    </div>
                        <v-text-field
                        :label="''+i"
                        required
                        v-model="select[i]"
                        ></v-text-field>
                    </v-col>
                    <v-btn @click="add"></v-btn>
                    <v-btn @click="subtraction"></v-btn>
                </v-row>
            </v-container>
            </v-card-text>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                color="blue darken-1"
                text
                @click="dialog = false"
            >
                
            </v-btn>
            <v-btn
                color="blue darken-1"
                text
                @click="addWords"
            >
                
            </v-btn>
            </v-card-actions>
        </v-card>
        </v-dialog>
    </v-row>
    </template>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return {
            dialog : false,
            select: [null],
            count : 1,
            name : null,
            files: [null],
            fileName:[null],
            index : 1
        }
    },
    methods : {
        add(){
            this.count=this.count+1;
            this.select.push(null)
            this.files.push(null)
            this.fileName.push(null)
        },
        subtraction(){
            if(this.count==1){
                return;
            }
            this.files.pop()
            this.select.pop()
            this.fileName.pop()
            this.count = this.count-1;
        },
        addWords(){
            if(this.select.length!=this.count+1){
            alert(' 1')
            return;
            }
            if(this.files.length!=this.count+1){
            alert(' 1')
            return;
            }
            for(let i=1;i<this.count+1;i++){
                if(this.select[i]==null){
                    alert(' 2')
                    return;
                }
            }
            for(let i=1;i<this.count+1;i++){
                if(this.files[i]==null){
                    alert(' 2')
                    return;
                }
            }
            this.dialog = false
            if(this.name==null){
                alert(' ')
                return;
            }
            // let configimg ={
            // header : {
            //     'Content-Type' : 'multipart/form-data',
            //     }
            // };
            // let form = new FormData();
            // form.append("file",this.files[1])
            // form.append("wordName",this.select)

            axios.post('wordtest/userword',{
                wordsetTitle : this.name,
                wordImgData: this.files,
                problemName : this.select
            }) //
            .then((data) =>{
                data
                alert(' .')
                this.intervalid = setInterval(()=>{
                this.$store.state.loadingflag = false;
                location.reload()
            }, 500);
            }).catch((error)=>{
                console.log(error)
            })
        },
        onFileChange(e) {
            // 
            console.dir(e.target.files)
            let files = e.target.files || e.dataTransfer.files;
            if (!files.length) return;
            // console.log(files[0])
            // var check = /[-|-|-]/;
            // if(check.test(files[0].name)) {
                //   this.classimg="";
            //   this.hide();
            //   alert('    ');
            //   return;
            // }
            this.createImage(files[0],this.index);
        },
        createImage(file,index) {
            // 
            // console.log(''+file.name)
            this.fileName.splice(index,1,file.name);
            let reader = new FileReader();
            let vm = this;
            reader.onload = (e) => {
                vm.classimg = e.target.result;
                this.files.splice(this.index,1,e.target.result);
            };
            reader.readAsDataURL(file);

        },
    }
}
</script>

